<template>
  <div class="container text-center">
    <div class="card" v-for="(group, groupIndex) in grouped" :key="groupIndex">
      <div class="row">
        <div
          class="col-4"
          v-for="(user, userIndex) in group"
          :key="`${groupIndex}-${userIndex}`"
          @click="open_user_profile(user.id)"
        >
          <img class="img-fluid" :src="user.photo" alt="User Photo" />
          <div class="card-body">
            <h6 class="card-title">{{ user.nickname }}</h6>
            <p class="card-text">{{ user.hprice }}元/时</p>
            <p class="card-text">{{ user.province }} {{ user.city }}</p>
            <!-- <p>{{ subject }}</p> -->
          </div>
        </div>
      </div>
    </div>
  </div>
</template>  
<script>
import { computed, ref, watchEffect } from "vue";
import $ from "jquery";
import router from "@/router/index";
import { useStore } from "vuex";
export default {
  name: "TeacherList",
  props: {
    teacher_model: {
      type: String,
      default: "0"
    },
    selectedProvince: {
      type: String,
      default: ""
    },
    selectedCity: {
      type: String,
      default: ""
    },
    selectedDistrict: {
      type: String,
      default: ""
    },
    classname: {
      type: String,
      default: ""
    },
    tagname: {
      type: String,
      default: ""
    }
  },
  mounted() {
    console.log(this)
    },
  setup(props) {
    const store = useStore();
    const users = ref([]);
  
    const get_users = () => {
      console.log(props.classname)
      // 构建请求参数
      const requestData = {
        subject: "",
        teacher_type: props.teacher_model,
      };

      // 添加科目参数
      if (props.classname && props.classname !== "全部") {
        requestData.classname = props.classname;
      }
      console.log(props.tagname)

      // 添加标签参数
      if (props.tagname && props.tagname !== "全部") {
        requestData.tagname = props.tagname;
      }

      // 添加地区参数
      if (props.selectedProvince) {
        requestData.province = props.selectedProvince;
      }
      if (props.selectedCity) {
        requestData.city = props.selectedCity;
      }
      if (props.selectedDistrict) {
        requestData.county = props.selectedDistrict;
      }
      console.log(requestData)
      $.ajax({
        url: "http://47.109.76.15:8000/teacher/teacherlist/",
        type: "get",
        data: requestData,
        success: (resp) => {
          console.log(resp);
          users.value = resp;
        },
      });
    };

    // 监听所有筛选条件的变化
    watchEffect(() => {
      get_users();
    });

    const grouped = computed(() => {
      const chunkSize = 3;
      // 使用reduce方法将数组分组
      return users.value.reduce((acc, curr, index) => {
        const chunkIndex = Math.floor(index / chunkSize);
        if (!acc[chunkIndex]) {
          acc[chunkIndex] = [];
        }
        acc[chunkIndex].push(curr);
        return acc;
      }, []);
    });
    const open_user_profile = (userId) => {
      if (store.state.user.is_login) {
        router.push({
          name: "teacherinfo",
          params: {
            userId,
          },
        });
      } else {
        router.push({
          name: "login",
        });
      }
    };

    return {
      grouped,
      open_user_profile,
    };
  },
};
</script>

<style scoped>
img {
  max-width: 150px;
  max-height: 150px;
  cursor: pointer;
}
.card {
  margin-top: 20px;
  padding-top: 20px;
  border: none;
}

.card-body{
  border: none;
}
</style>
