<template>
  <div class="container">
    <div class="card">
      <div class="card-body">
        <div class="row">
          <div class="col-3 img-field">
            <img class="img-fluid" :src="user.photo" alt="" />
          </div>
          <div class="col-6">
            <div class="nickname dd">{{ user.nickname }}</div>
            <div class="hprice dd">{{ user.hprice }}元/时</div>
            <div class="subject dd">科目 : {{ user.subject }}</div>
            <div class="subject dd">学历 : {{ user.eq }}</div>
            <div class="subject dd">邮箱 : {{ user.email }}</div>
            <div class="subject dd">年龄 : {{ user.age }}岁</div>
            <div class="subject dd">
              地区 : {{ user.province }}{{ user.city }}
            </div>
            <button type="submit" class="btn btn-primary dd">立即联系</button>
          </div>
          <div class="col-3">
            <ui>
              <li>
                <i class="bi bi-heart-fill">加入心愿单</i>
              </li>
              <li>
                <i class="bi bi-star-fill">收藏</i>
              </li>
              <li>
                <i class="bi bi-share">分享</i>
              </li>
            </ui>
          </div>
        </div>
      </div>
    </div>
    <div class="card">
      <div class="card-body">
        <div class="row">
          <div class="col-9">
            <label for="exampleFormControlTextarea1" class="form-label"
              >简介</label
            >
            <textarea
              class="form-control"
              disabled
              rows="3"
              v-model="user.remark"
            >
          </textarea>
          </div>
          <div class="col-3">相关推荐</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { reactive } from "vue";
import { useStore } from "vuex";
import { useRoute } from "vue-router";
import $ from "jquery";

export default {
  name: "TeacherInfo",
  setup() {
    const store = useStore();
    const route = useRoute();
    const userId = parseInt(route.params.userId);
    const user = reactive({});
    $.ajax({
      url: "http://47.109.76.15:8000/teacher/teacherinfo/",
      type: "GET",
      data: {
        user_id: userId,
      },
      headers: {
        Authorization: "Bearer " + store.state.user.access,
      },
      success(resp) {
        user.id = resp.id;
        user.nickname = resp.nickname;
        user.photo = resp.photo;
        user.followerCount = resp.followerCount;
        user.is_followed = resp.is_followed;
        user.email = resp.email;
        user.eq = resp.eq
        user.subject = resp.subject
        user.remark = resp.remark
        user.hprice = resp.hprice;
        user.age = resp.age;
        user.province = resp.province;
        user.city = resp.city;
      },
    });
    return {
      user,
    };
  },
};
</script>
<style scoped>
img {
  border-radius: 50%;
}

.nickname {
  font-weight: bold;
}

.fans {
  font-size: 12px;
  color: gray;
}

button {
  padding: 2px 4px;
  font-size: 12px;
}

.img-field {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.dd {
  margin-top: 15px;
  margin-left: 30px;
}
li {
  height: 50px;
  list-style: none;
}
textarea{
  resize: none;
}
</style>