<template>
  <div class="card">
      <div class="subject-categories">
        <h5 class="mb-3 text-center">科目分类</h5>
        <ul class="list-group text-center">
          <li
            v-for="(subject, index) in subjects"
            :key="index"
            class="list-group-item border-0"
            :class="{ active: selectedSubject === subject }"
            @click="selectSubject(subject)"
          >
            {{ subject }}
          </li>
        </ul>
      </div>
  </div>
</template>

<script>
import {ref} from 'vue'
import $ from 'jquery'
export default {
  name: "SubjectList",
  emits: ['update-subject'],  // 声明要发出的事件
  setup(props,{emit}){
    const subjects = ref(["全部"])
    const selectedSubject =  ref("全部")
    const selectSubject = (subject) =>{
      selectedSubject.value = subject
      emit('update-subject', selectedSubject.value);

    }

    const getSubjects = () =>{
      $.ajax({
        url:'http://47.109.76.15:8000/classification/classificationlist/',
        type:'get',
        success(resp){
          console.log('getSubjects----',resp)
          resp.forEach(r => {
            if (r.classname) { 
              subjects.value.push(r.classname);
            }
          });
        }
      })
    }
    getSubjects()
    return {
      subjects,
      selectedSubject,
      selectSubject
    }
  }

};
</script>

<style scoped>
.subject-categories {
  width: 150px;
  margin: 0 auto;
}

h5 {
  font-size: 0.9rem;
  font-weight: bold;
}

.list-group-item {
  cursor: pointer;
  transition: color 0.2s ease;
  background-color: transparent !important;
  font-size: 0.8rem;
  /* padding: 0.3rem 0; */
}

.list-group-item:hover {
  color: #0056b3;
}

.list-group-item.active {
  font-weight: bold;
  color: #007bff !important;
}
</style>