<template>
  <div class="container mt-4">
    <h5 class="mb-3 fw-bold">热门标签</h5>
    <div class="tag-container">
      <button
        v-for="(tag,index) in tags"
        :key="index"
        @click="selectTag(index)"
        :class="[
          'btn',
          'btn-sm',
          'rounded-pill',
          'me-2',
          'mb-2',
          selectedTagId === index ? 'btn-primary' : 'btn-outline-secondary',
        ]"
      >
        {{ tag.tagname }}
      </button>
    </div>
  </div>
</template>

<script>
import { ref } from "vue";
import $ from "jquery";
export default {
  name: "HotLable",
  emits: ['update-tag'],  // 声明要发出的事件
  setup(props, context) {
    const tags = ref([]);
    const tagname = ref("");
    $.ajax({
      url: "http://47.109.76.15:8000/tag/taglist/",
      type: "get",
      success: (resp) => {
        tags.value = resp;
        console.log(resp)
      },
    });
    const selectedTagId = ref(0); 

    const selectTag = (tagId) => {
      selectedTagId.value = tagId;
      tagname.value = tags.value[tagId].tagname;
      console.log('tagname.value------',tagname.value)
      context.emit("update-tag", tagname.value);
    };
    return {
      tags,
      selectedTagId,
      selectTag,
      tagname,
    };
  },
};
</script>

<style scoped>
.tag-container {
  display: flex;
  flex-wrap: wrap;
}

.btn-outline-secondary {
  color: #6c757d;
  border-color: #6c757d;
  background-color: white;
}

.btn-outline-secondary:hover {
  color: #6c757d;
  background-color: #f8f9fa;
}

.btn-primary {
  background-color: #0d6efd;
  border-color: #0d6efd;
}

.btn-sm {
  padding: 0.25rem 0.5rem;
  font-size: 0.875rem;
  line-height: 1.5;
}
</style>