<template>
  <ContentBase>
    <div class="row justify-content-md-center">
      <div class="col-3">
        <form @submit.prevent="login">
          <div class="mb-3 login-type-buttons">
            <button 
              type="button" 
              class="btn" 
              :class="{'btn-primary': loginType === 'student', 'btn-outline-primary': loginType !== 'student'}"
              @click="loginType = 'student'"
            >
              学生登录
            </button>
            <button 
              type="button" 
              class="btn" 
              :class="{'btn-primary': loginType === 'teacher', 'btn-outline-primary': loginType !== 'teacher'}"
              @click="loginType = 'teacher'"
            >
              教师登录
            </button>
          </div>
          <div class="input-group mb-3">
            <span class="input-group-text">{{ loginType === 'teacher' ? '教员名' : '用户名' }}</span>
            <input
              v-model="username"
              type="text"
              class="form-control"
              id="username"
            />
          </div>
          <div class="input-group mb-3">
            <span class="input-group-text">密码</span>
            <input
              v-model="password"
              type="password"
              class="form-control"
              id="password"
            />
          </div>
          <div class="error-message">{{ error_message }}</div>
          <button type="submit" class="btn btn-primary">登录</button>
        </form>

        <!-- 动态注册按钮 -->
        <div class="mt-3 text-center">
          <router-link 
            :to="{ name: loginType === 'teacher' ? 'register_teacher' : 'register' }" 
            class="btn btn-link"
          >
            还没有账号？{{ loginType === 'teacher' ? '注册教员' : '注册用户' }}
          </router-link>
        </div>
      </div>
    </div>
  </ContentBase>
</template>
    

<script>
import ContentBase from "../components/ContentBase";
import { ref } from "vue";
import { useStore } from "vuex";
import router from "@/router/index";

export default {
  name: "LoginView",
  components: {
    ContentBase,
  },
  setup() {
    const store = useStore();
    let username = ref("");
    let password = ref("");
    let error_message = ref("");
    let loginType = ref("student"); // 默认为学生登录

    const login = () => {
      error_message.value = "";
      store.dispatch("login", {
        username: username.value,
        password: password.value,
        loginType: loginType.value,
        success() {
          router.push({ name: "home" });
        },
        error() {
          error_message.value = "用户名或密码错误";
        },
      });
    };

    return {
      username,
      password,
      error_message,
      login,
      loginType,
    };
  },
};
</script>

<style scoped>
.login-type-buttons {
  display: flex;
  justify-content: space-between;
  margin: 0 20px;
}

.login-type-buttons .btn {
  padding: 8px 30px;
}

button[type="submit"] {
  width: 100%;
}

.error-message {
  color: red;
}

.btn-link {
  text-decoration: none;
}

.btn-link:hover {
  text-decoration: underline;
}
</style>