<template>
  <ContentBase>
    <div class="row justify-content-md-center">
      <div class="col-6">
        <form @submit.prevent="register">
          <div class="input-group mb-3">
            <span class="input-group-text">用户名 </span>
            <input
              v-model="username"
              type="text"
              class="form-control"
              id="username"
            />
          </div>
          <div class="input-group mb-3">
            <span class="input-group-text">密码 </span>
            <input
              v-model="password"
              type="password"
              class="form-control"
              id="password"
            />
          </div>
          <div class="input-group mb-3">
            <span class="input-group-text">确定密码 </span>
            <input
              v-model="password_confirm"
              type="password"
              class="form-control"
              id="password"
            />
          </div>
          <div class="error-message">{{ error_message }}</div>
          <button type="submit" class="btn btn-primary">注册</button>
        </form>

        <div class="mt-3 text-center">
          <router-link :to="{ name: 'login' }" class="btn btn-link">
            已有账号？返回登录
          </router-link>
        </div>
      </div>
    </div>
  </ContentBase>
</template>

<script>
import ContentBase from "../components/ContentBase";
import { ref } from "vue";
import { useStore } from "vuex";
import router from "@/router/index";
import $ from "jquery";

export default {
  name: "RegisterView",
  components: {
    ContentBase,
  },
  setup() {
    const store = useStore();
    let username = ref("");
    let password = ref("");
    let password_confirm = ref("");
    let error_message = ref("");

    const register = () => {
      error_message.value = "";
      $.ajax({
        url: "http://47.109.76.15:8000/user/register/",
        type: "POST",
        data: {
          username: username.value,
          password: password.value,
          password_confirm: password_confirm.value,
        },
        success(resp) {
          if (resp.result === "success") {
            store.dispatch("login", {
              username: username.value,
              password: password.value,
              success() {
                router.push({ name: "home" });
              },
              error() {
                error_message.value = "系统异常，请稍后重试";
              },
            });
          } else {
            error_message.value = resp.result;
          }
        },
      });
    };

    return {
      username,
      password,
      password_confirm,
      error_message,
      register,
    };
  },
};
</script>

<style scoped>
button {
  width: 100%;
}

.error-message {
  color: red;
}

.bi-arrow-left {
  margin-right: 5px;
}

.btn-link {
  text-decoration: none;
}

.btn-link:hover {
  text-decoration: underline;
}
</style>