import $ from "jquery";
import { jwtDecode } from "jwt-decode";

const ModuleUser = {
  state: {
    id: "",
    username: "",
    photo: "",
    followerCount: 0,
    access: "",
    refresh: "",
    is_login: false,
    is_teacher: false,
  },
  getters: {},
  mutations: {
    updateUser(state, user) {
      state.id = user.id;
      state.username = user.username;
      state.photo = user.photo;
      state.followerCount = user.followerCount;
      state.access = user.access;
      state.refresh = user.refresh;
      state.is_login = user.is_login;
      state.is_teacher = user.is_teacher;
    },
    updateAccess(state, access) {
      state.access = access;
    },
    logout(state) {
      state.id = "";
      state.username = "";
      state.photo = "";
      state.followerCount = 0;
      state.access = "";
      state.refresh = "";
      state.is_login = false;
      state.is_teacher = false;
    },
  },
  actions: {
    login(context, data) {
      const loginUrl = data.loginType === 'teacher' 
        ? "http://47.109.76.15:8000/teacher/token/"
        : "http://47.109.76.15:8000/user/token/";

      $.ajax({
        url: loginUrl,
        type: "POST",
        data: {
          username: data.username,
          password: data.password,
        },
        success(resp) {
          console.log('resp--------------',resp)
          const { access, refresh } = resp;
          const access_obj = jwtDecode(access);

          setInterval(() => {
            $.ajax({
              url: data.loginType === 'teacher'
                ? "http://47.109.76.15:8000/teacher/token/refresh/"
                : "http://47.109.76.15:8000/user/token/refresh/",
              type: "POST",
              data: {
                refresh,
              },
              success(resp) {
                context.commit("updateAccess", resp.access);
              },
            });
          }, 4.5 * 60 * 1000);

          console.log('access_obj    : ',access_obj)
          $.ajax({
            url: data.loginType === 'teacher'
              ? "http://47.109.76.15:8000/teacher/teacherinfo/"
              : "http://47.109.76.15:8000/user/userinfo/",
            type: "GET",
            data: {
              user_id: access_obj.user_id,
            },
            headers: {
              Authorization: "Bearer " + access,
            },
            success(resp) {
              console.log('updateUser',resp)
              context.commit("updateUser", {
                ...resp,
                access: access,
                refresh: refresh,
                is_login: true,
                is_teacher: data.loginType === 'teacher'
              });
              console.log('is_teacher value:', data.loginType === 'teacher');
              console.log('store state after update:', context.state.is_teacher);
              data.success();
            },
          });
        },
        error() {
          data.error();
        },
      });
    },
  },
  modules: {},
};

export default ModuleUser;
