<template>
  <ContentBase>
    <div class="row justify-content-md-center">
      <div class="col-6">
        <form @submit.prevent="register">
          <div class="input-group mb-3">
            <span class="input-group-text">用户名</span>
            <input v-model="username" type="text" class="form-control" id="username" required>
          </div>

          <div class="input-group mb-3">
            <span class="input-group-text">真实姓名</span>
            <input v-model="realname" type="text" class="form-control" required>
          </div>

          <div class="input-group mb-3">
            <span class="input-group-text">邮箱</span>
            <input v-model="email" type="email" class="form-control" required>
          </div>

          <div class="input-group mb-3">
            <span class="input-group-text">手机号</span>
            <input v-model="phone" type="tel" class="form-control" required>
          </div>

          <div class="input-group mb-3">
            <span class="input-group-text">密码</span>
            <input v-model="password" type="password" class="form-control" id="password" required>
          </div>

          <div class="input-group mb-3">
            <span class="input-group-text">确认密码</span>
            <input v-model="confirmPassword" type="password" class="form-control" id="confirmPassword" required>
          </div>

          <div class="input-group mb-3">
            <span class="input-group-text">年龄</span>
            <input v-model.number="age" type="number" class="form-control" id="age" required>
          </div>

          <div class="input-group mb-3">
            <span class="input-group-text">性别</span>
            <select v-model="gender" class="form-select" id="gender" required>
              <option value="male">男</option>
              <option value="female">女</option>
            </select>
          </div>

          <div class="input-group mb-3">
            <span class="input-group-text">学历</span>
            <select v-model="education" class="form-select" required>
              <option value="">请选择学历</option>
              <option v-for="option in educationOptions" 
                      :key="option.value" 
                      :value="option.value">
                {{ option.label }}
              </option>
            </select>
          </div>

          <div class="input-group mb-3">
            <span class="input-group-text">时薪（元/小时）</span>
            <input v-model.number="hourlyRate" type="number" class="form-control" id="hourlyRate" required>
          </div>

          <div class="input-group mb-3">
            <span class="input-group-text">所在地区</span>
            <select v-model="selectedProvinceCode" class="form-select" required>
              <option value="">选择省份</option>
              <option v-for="(name, code) in provinces" :key="code" :value="code">
                {{ name }}
              </option>
            </select>
            <select v-model="selectedCityCode" class="form-select" required>
              <option value="">选择城市</option>
              <option v-for="(name, code) in citys" :key="code" :value="code">
                {{ name }}
              </option>
            </select>
            <select 
              v-if="Object.keys(countrys).length > 0" 
              v-model="selectedCountryCode" 
              class="form-select" 
              required
            >
              <option value="">选择区县</option>
              <option v-for="(name, code) in countrys" :key="code" :value="code">
                {{ name }}
              </option>
            </select>
          </div>

          <div class="input-group mb-3">
            <span class="input-group-text">自我描述</span>
            <textarea v-model="description" class="form-control" id="description" rows="3"></textarea>
          </div>

          <div class="error-message">{{ error_message }}</div>
          <button type="submit" class="btn btn-primary">注册</button>
        </form>

        <div class="mt-3 text-center">
          <router-link :to="{ name: 'login' }" class="btn btn-link">
            已有账号？返回登录
          </router-link>
        </div>
      </div>
    </div>
  </ContentBase>
</template>

<script>
import ContentBase from "../components/ContentBase";
import { ref, watchEffect } from "vue";
import { useStore } from "vuex";
import router from "@/router/index";
import $ from 'jquery';

export default {
  name: "RegisterTeacherView",
  components: {
    ContentBase,
  },
  setup() {
    const store = useStore();
    let username = ref("");
    let realname = ref("");
    let password = ref("");
    let confirmPassword = ref("");
    let age = ref(18);  // 默认18岁
    let gender = ref("male");  // 默认男
    let education = ref("");
    let hourlyRate = ref(100);  // 默认100元/小时
    let description = ref("");
    let error_message = ref("");

    // 省市区相关的 ref
    const selectedProvinceCode = ref('110000');
    const provinces = ref([]);

    const selectedCityCode = ref('110101');
    const citys = ref([]);
    
    const selectedCountryCode = ref('');
    const countrys = ref({});  // 改为空对象而不是空数组

    let email = ref("");
    let phone = ref("");

    const get_provinces = () => {
      $.ajax({
        url: "http://47.109.76.15:8000/tool/provinces/",
        type: "get",
        success: (resp) => {
          provinces.value = resp;
        },
      });
    };

    const get_citys = (code) => {
      if (!code) {
        citys.value = {};
        selectedCityCode.value = '';
        return;
      }
      $.ajax({
        url: "http://47.109.76.15:8000/tool/cities/",
        type: "get",
        data: {
          'province_code': code
        },
        success: (resp) => {
          citys.value = resp;
          selectedCityCode.value = Object.keys(resp)[0];
        },
      });
    };

    const get_country = (code) => {
      if (!code) {
        countrys.value = {};
        selectedCountryCode.value = '';
        return;
      }
      $.ajax({
        url: "http://47.109.76.15:8000/tool/country/",
        type: "get",
        data: {
          'city_code': code
        },
        success: (resp) => {
          countrys.value = resp || {};  // 如果返回空，设置为空对象
          if (Object.keys(countrys.value).length > 0) {
            selectedCountryCode.value = Object.keys(resp)[0];
          } else {
            selectedCountryCode.value = '';
          }
        },
      });
    };

    // 监听省份变化
    watchEffect(() => {
      if (selectedProvinceCode.value) {
        get_citys(selectedProvinceCode.value);
      } else {
        citys.value = {};
        countrys.value = {};
        selectedCityCode.value = '';
        selectedCountryCode.value = '';
      }
    });

    // 监听城市变化
    watchEffect(() => {
      if (selectedCityCode.value) {
        get_country(selectedCityCode.value);
      } else {
        countrys.value = {};
        selectedCountryCode.value = '';
      }
    });

    // 初始化获取省份数据
    get_provinces();

    const validateForm = () => {
      if (!username.value) {
        error_message.value = "请输入用户名";
        return false;
      }
      if (!realname.value) {
        error_message.value = "请输入真实姓名";
        return false;
      }
      if (!email.value) {
        error_message.value = "请输入邮箱";
        return false;
      }
      if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(email.value)) {
        error_message.value = "请输入有效的邮箱地址";
        return false;
      }
      if (!phone.value) {
        error_message.value = "请输入手机号";
        return false;
      }
      if (!/^1[3-9]\d{9}$/.test(phone.value)) {
        error_message.value = "请输入有效的手机号";
        return false;
      }
      if (!password.value) {
        error_message.value = "请输入密码";
        return false;
      }
      if (password.value !== confirmPassword.value) {
        error_message.value = "两次输入的密码不一致";
        return false;
      }
      if (!age.value) {
        error_message.value = "请输入年龄";
        return false;
      }
      if (!gender.value) {
        error_message.value = "请选择性别";
        return false;
      }
      if (!education.value) {
        error_message.value = "请选择学历";
        return false;
      }
      if (!hourlyRate.value) {
        error_message.value = "请输入时薪";
        return false;
      }
      if (!selectedProvinceCode.value || !selectedCityCode.value) {
        error_message.value = "请选择所在地区";
        return false;
      }
      if (Object.keys(countrys.value).length > 0 && !selectedCountryCode.value) {
        error_message.value = "请选择区县";
        return false;
      }
      return true;
    };

    // 将学历值转换为对应的数字
    const getEducationValue = (education) => {
      const educationMap = {
        'primary': 0,    // 小学
        'junior': 1,     // 初中
        'senior': 2,     // 高中
        'bachelor': 3,   // 本科
        'master': 4,     // 硕士
        'doctor': 5      // 博士
      };
      return educationMap[education] || 3;  // 默认本科
    };

    const register = () => {
      error_message.value = "";
      
      if (!validateForm()) {
        return;
      }

      // 准备发送的数据
      const registerData = {
        username: username.value,
        password: password.value,
        password_confirm: confirmPassword.value,
        nickname: realname.value,
        age: age.value.toString(),
        gender: gender.value === 'male' ? '0' : '1',
        eq: getEducationValue(education.value).toString(),
        status: '0',
        email: email.value,
        phonenumber: phone.value,
        province: provinces.value[selectedProvinceCode.value],  // 使用省份名称
        city: citys.value[selectedCityCode.value],             // 使用城市名称
        county: Object.keys(countrys.value).length > 0 
               ? countrys.value[selectedCountryCode.value]      // 使用区县名称
               : citys.value[selectedCityCode.value],          // 如果没有区县，使用城市名称
        area: description.value || '',
        hprice: hourlyRate.value.toString(),
      };

      console.log('Sending registration data:', registerData);  // 调试用

      $.ajax({
        url: "http://47.109.76.15:8000/teacher/register/",
        type: "POST",
        data: registerData,
        success(resp) {
          console.log('Registration response:', resp);
          if (resp.result === "add teacher success") {
            store.dispatch("login", {
              username: username.value,
              password: password.value,
              loginType: 'teacher',
              success() {
                router.push({ name: "home" });
              },
              error() {
                error_message.value = "系统异常，请稍后重试";
              },
            });
          } else {
            error_message.value = resp.result;
          }
        },
        error(xhr, status, error) {
          console.log('Registration error:', error);
          error_message.value = "注册失败，请稍后重试";
        }
      });
    };

    // 修改学历选项以匹配后端要求
    const educationOptions = [
      { value: 'primary', label: '小学' },
      { value: 'junior', label: '初中' },
      { value: 'senior', label: '高中' },
      { value: 'bachelor', label: '本科' },
      { value: 'master', label: '硕士' },
      { value: 'doctor', label: '博士' }
    ];

    return {
      username,
      realname,
      password,
      confirmPassword,
      age,
      gender,
      education,
      hourlyRate,
      description,
      error_message,
      register,
      selectedProvinceCode,
      selectedCityCode,
      selectedCountryCode,
      provinces,
      citys,
      countrys,
      email,
      phone,
      educationOptions,  // 返回学历选项
    };
  },
};
</script>

<style scoped>
button[type="submit"] {
  width: 100%;
}

.error-message {
  color: red;
}

.input-group-text {
  width: 120px;  /* 固定标签宽度 */
  justify-content: center;  /* 文字居中 */
}

textarea {
  resize: vertical;  /* 只允许垂直调整大小 */
}

.input-group select {
  flex: 1;  /* 让每个select占据相等的空间 */
  margin: 0 2px;  /* 添加一点间距 */
}

.input-group select:first-of-type {
  margin-left: 0;  /* 第一个select不需要左边距 */
}

.input-group select:last-of-type {
  margin-right: 0;  /* 最后一个select不需要右边距 */
}

.bi-arrow-left {
  margin-right: 5px;
}
</style> 